::-webkit-scrollbar {
    width: 10px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #1e293b; 
  }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(82, 82, 82); 
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: rgb(115, 115, 115); 
  }